body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 15px;
  padding: 5px;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  background-color: #0d1b2a;
}

h1 {
  font-size: 45px;
  font-weight: bolder;
  margin: 20px;
  margin-top: 200px;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  color: #778da9;
  text-shadow: 1px 1px 5px #1b263b 0px 0px 25px #415a77 0px 0px 15px #778da9;
}

form {
  margin: 15px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}

input {
  margin: 10px;
  padding: 5px;
  max-width: 600px;
  min-width: 200px;
  min-height: 35px;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1b263b;
  color: #e0e1dd;
  border: 3px solid #e0e1dd;
  border-radius: 7px;
  box-shadow: 1px 1px 5px #1b263b 0px 0px 25px #415a77 0px 0px 15px #778da9;
}

button {
  margin: 10px;
  padding: 5px;
  max-width: 200px;
  min-width: 75px;
  min-height: 35px;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1b263b;
  color: #e0e1dd;
  border: 3px solid #e0e1dd;
  border-radius: 7px;
  box-shadow: 1px 1px 5px #1b263b 0px 0px 25px #415a77 0px 0px 15px #778da9;
}
button:hover {
  transform: scale(1.1);
  transition: ease 0.5ms;
  box-shadow: 3px 3px 5px #1b263b 0px 0px 25px #415a77 2px 2px 15px #778da9;
}

.weather {
  font-size: 28px;
  margin: 25px;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  color: #778da9;
  text-shadow: 1px 1px 5px #1b263b 0px 0px 25px #415a77 0px 0px 15px #778da9;
}

#weatherIcon {
  margin: 15px;
  width: auto;
  max-height: 25vh;
  min-height: 20vh;
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  margin-top: 75px;
  color: #e0e1dd;
}

.footerLogo {
  width: 20px;
  height: 20px;
  margin-left: 5px;
  filter: invert(1);
}
